import React, {useState, useEffect } from 'react'

const Creatorstuff = (props) => {
  const [thing, setThing] = useState('');
  const [hi, setHi] = useState('');
  useEffect(() => {
    fetch('/api/users/userdatafromid?id=' + props.id)
    .then(res => res.json())
    .then(json => {
      //console.log(json);
      //console.log(props.id)
      setThing(<React.Fragment>{json.user.name}</React.Fragment>);
      console.log(thing);
    })
  }, [hi]);
  
  return thing;
}

export default Creatorstuff