import React, { Component } from 'react'
import {Redirect} from 'react-router-dom'
import './css/Header.css' 
import {Link} from 'react-router-dom'
import {MyContext} from '../storage'

export class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      userdata: {},
      search: '',
      url: ''
    };

    this.onSearchChange = this.onSearchChange.bind(this);
    this.change = this.change.bind(this);
  }

  onSearchChange(e) {
    this.setState({
      search: e.target.value
    });
  }
  
  change() {
    this.setState({
      url: '/search/' + this.state.search
    });
  }

  componentDidMount() {
    this.setState({userdata: this.context.state.userData});
    if (this.props.search) {
      this.setState({
        search: this.props.search
        
      });
    }
    console.log(this.context.state.userData);
  }

  render() {
    
    let style1 = {
      textDecoration: 'none',
      color: 'white'
      
    }

    let thingy = <img id="profileico" src='../images/profile.png' alt="" />;

    return (
      <div className="wrapper">
        {this.state.url !== '' ? <Redirect to={this.state.url} /> : null}
        <div className="top-text">
          <div className="grid-item">
            <h1 id="title">
              <Link to="/" style={style1}>EeeErr</Link>
            </h1>
          </div>
          <div className="grid-item">
            <div id="search-bar">
              <input id="search-bar-input" value={this.state.search} onChange={this.onSearchChange} type="search" />
              <button onClick={this.change} id="search-bar-button">search</button>
            </div>
          </div>
          <div className="grid-item">
            <Link to="/upload">
              <img id="uploadico" src='../images/upload.png' alt="" />
            </Link>
          </div>
          <div className="grid-item">
            <Link to={'/profile/' + this.state.userdata.name}>
              {thingy}
            </Link>
          </div>

        </div>
      </div>
    )
  }
}

export default Header
Header.contextType = MyContext
