import React, { Component } from 'react'
import {Link} from 'react-router-dom'

import Header from './Header.js'
import Footer from './Footer.js'
import Creatorstuff from './Creatorstuff'

import './css/Search.css'

import {MyContext} from '../storage'

export class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      query: this.props.match.params.query,
      videos: null
    };
  }

  componentWillReceiveProps(nextProps) {
    fetch('/api/videos/search?q=' + nextProps.match.params.query)
      .then(res => res.json())
      .then(json => {
        this.setState({
          videos: json.videos
        });
      })
  }

  componentDidMount() {
    fetch('/api/videos/search?q=' + this.state.query)
      .then(res => res.json())
      .then(json => {
        this.setState({
          videos: json.videos
        });
      })
  }

  render() {
    return (
      <div>
        <div className="top-bg">
          <Header />

          <div id="recommended-search">

            {this.state.videos ?
              this.state.videos.map((item, i) => {
                
                return (
                  <div className="recommended-item" key={i}>
                    <center>
                      <Link to={'/video/' + item._id}>
                        <img width="272px" height="153px" src={"http://138.68.12.208/uploads/" + item.thumbnail} alt=""/>
                      </Link>
                      <div style={{display: 'grid', gridTemplateColumns: '50% 50%'}}>
                        <Link style={{textDecoration: 'none', color: 'white'}} to={"/video/" + item._id}><p>{item.title}</p></Link>
                        <Link style={{textDecoration: 'none', color: 'gray'}} to={"/video/" + item._id}><p><Creatorstuff id={item.creator} /></p></Link>
                      </div>
                    </center>
                  </div>
                )
              })
              : null
            }
            
          </div>

          <Footer />
        </div>
      </div>
    )
  }
}

export default Search
Search.contextType = MyContext
