export function getSomeStuffs(token1, cb) {

  fetch('/api/users/userdata?token=' + token1)
    .then(res => res.json())
    .then(json => cb(json))
}

export function getUserFromId(id, cb) {
  fetch('/api/users/userdatafromid?id=' + id)
    .then(res => res.json())
    .then(json => cb(json))
}


