import React, { Component } from 'react'
import { Redirect, Route, HashRouter as Router, Switch } from 'react-router-dom'

import Home from "./components/Home";
import Register from "./components/Register"
import Profile from './components/Profile'
import Upload from './components/Upload'
import Video from './components/Video'
import Search from './components/Search'

import './App.css'

import Bg from './components/top-bg.jpg'

import { MyContext } from './storage'
import { getSomeStuffs } from './utils/somefuncs'
import {
  getFromStorage
} from './utils/storage';

export class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      error: '',
      token: this.props.token
    };

    this.setParentToken = this.setParentToken.bind(this);
    this.func1 = this.func1.bind(this);
  }

  setParentToken(t) {
    this.setState({ token: t });
  }

  componentDidMount() {
    //this.setState({isLoading: true});
    const obj = getFromStorage('eee_err');
    if (obj && obj.token) {

      //verify
      fetch('/api/users/verify?token=' + obj.token)
        .then(res => res.json())
        .then(json => {
          if (json.success) {


            this.context.changeToken(obj.token);
            getSomeStuffs(obj.token, (data) => {
              if (data.message) {
                console.log(data.message)
              } else {
                this.context.setUserData(data);
                this.setState({
                  token: obj.token,
                  isLoading: false
                });
              }
            });


          } else {
            this.setState({
              isLoading: false
            });
          }
        })

    } else {
      this.setState({
        isLoading: false
      });
    }
  }

  func1() {
    window.history.back();
    return null;
  }

  render() {



    let bgstyles = {
      backgroundImage: `url(${Bg})`,
      margin: '0',
      backgroundRepeat: 'repeat',
      minHeight: '100vh',
      backgroundSize: 'cover',

    };

    if (this.state.isLoading) {
      return (
        <div><p>loading...</p></div>
      )
    }

    return (
      <Router>
        <div className="wrapper" style={bgstyles}>
          <Switch>
            <Route path="/" exact strict render={
              () => (
                <React.Fragment>
                  {
                    this.state.token !== '' ? (<MyContext.Consumer>
                      {(context) => (

                        <Home setParentToken={this.setParentToken} token={context.state.token} />
                      )}

                    </MyContext.Consumer>) : <Redirect to="/auth" />
                  }
                </React.Fragment>
              )
            } />

            <Route path="/auth" exact strict render={
              () => (

                <React.Fragment>
                  {
                    this.state.token === '' ? <Register changeToken={(token) => this.setState({ token: token })} register={false} />
                      : <Redirect to="/" />
                  }
                </React.Fragment>
              )
            } />

            <Route path="/profile/:username" exact strict component={
              (props) => (
                this.state.token !== '' ? <Profile setParentToken={this.setParentToken} {...props} />
                  : <Redirect to="/auth" />
              )
            } />

            <Route path="/upload" exact strict render={
              () => (
                <React.Fragment>
                  {
                    this.state.token !== '' ? <Upload />
                      : <Redirect to="/auth" />
                  }
                </React.Fragment>
              )
            } />

            <Route path="/video/:id" exact strict component={
              (props) => (
                this.state.token !== '' ? <Video {...props} />
                  : <Redirect to="/auth" />
              )
            } />

            <Route path="/search/:query" exact strict component={
              (props) => (
                this.state.token !== '' ? <Search {...props} />
                  : <Redirect to="/auth" />
              )
            } />
          </Switch>
        </div>
      </Router>
    )
  }
}

export default App
App.contextType = MyContext

