import React, { Component } from 'react'
import {Link} from 'react-router-dom'

import Header from './Header.js'
import Footer from './Footer.js'
import Creatorstuff from './Creatorstuff'

import {MyContext} from '../storage'

import './css/Video.css'

export class Video extends Component {
  constructor(props) {
    super(props);

    this.state = {
      id: this.props.match.params.id,
      video: {
        likes: [],
        comments: []
      },
      userdata: {},
      creatordata: {},
      liked: false,
      subscribed: false,
      suggested: [],
      comment: ''
    };

    this.subscribe = this.subscribe.bind(this);
    this.like = this.like.bind(this);
    this.onComment = this.onComment.bind(this);
    this.submitComment = this.submitComment.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    this.setState({id: nextProps.match.params.id});

    fetch('/api/videos/one?id=' + nextProps.match.params.id)
      .then(res => res.json())
      .then(json => {
        if (json.success) {
          
          fetch('/api/users/userdatafromid?id=' + json.video.creator)
            .then(res2 => res2.json())
            .then(json2 => {
              this.setState({
                video: json.video, 
                userdata: this.context.state.userData,
                creatordata: json2.user,
                liked: false,
                subscribed: false
              });

              this.state.creatordata.subscribers.forEach(elem => {
                //console.log(elem, this.state.userdata._id);
                if (elem === this.state.userdata._id) {
                  this.setState({subscribed: true});
                }
                  
              });

              this.state.video.likes.forEach(thing => {
                if (thing === this.state.userdata._id) {
                  this.setState({liked: true});
                }
              });

              fetch('/api/videos/all')
                .then(res3 => res3.json())
                .then(json3 => {
                  this.setState({suggested: json3.videos});
                })
            })
        }
      });
  }

  componentDidMount() {
    fetch('/api/videos/one?id=' + this.state.id)
      .then(res => res.json())
      .then(json => {
        if (json.success) {
          
          fetch('/api/users/userdatafromid?id=' + json.video.creator)
            .then(res2 => res2.json())
            .then(json2 => {
              this.setState({
                video: json.video, 
                userdata: this.context.state.userData,
                creatordata: json2.user
              });

              this.state.creatordata.subscribers.forEach(elem => {
                //console.log(elem, this.state.userdata._id);
                if (elem === this.state.userdata._id) {
                  this.setState({subscribed: true});
                }
                  
              });

              this.state.video.likes.forEach(thing => {
                if (thing === this.state.userdata._id) {
                  this.setState({liked: true});
                }
              });

              fetch('/api/videos/all')
                .then(res3 => res3.json())
                .then(json3 => {
                  this.setState({suggested: json3.videos});
                })
            })
        }
      });
  }

  like() {
    const userid = this.state.userdata._id;
    const videoid = this.state.video._id;

    fetch('/api/videos/like', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      },
      body: JSON.stringify({
        videoid: videoid,
        userid: userid
      })
    }).then(res => res.json())
      .then(json => {
        if (json.success) {
          let yo = this.state.video
          let thing = yo.likes.push(userid);

          this.setState({
            liked: true,
            video: yo
          });
        } else {
          console.log('Error');
        }
      })
  }

  subscribe() {
    const userid1 = this.state.userdata._id;
    const name = this.state.creatordata.name;

    fetch('/api/users/subscribe', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      },
      body: JSON.stringify({
        name: name,
        userid1: userid1
      })
    }).then(res => res.json())
      .then(json => {
        if (json.success) {
          let yo = this.state.creatordata
          let thing = yo.subscribers.push(userid1);

          this.setState({
            subscribed: true,
            creatordata: yo
          });
        } else {
          console.log('Error');
        }
      })
  }

  onComment(e) {
    this.setState({
      comment: e.target.value
    });
  }

  submitComment() {
    const creatorid = this.state.userdata._id;
    const videoid = this.state.video._id;
    const content = this.state.comment;

    fetch('/api/comments/comment', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      },
      body: JSON.stringify({
        videoid: videoid,
        creatorid: creatorid,
        content: content
      })
    }).then(res => res.json())
      .then(json => {
        if (json.success) {
          let yo = this.state.video
          let thing = yo.comments.push(json.comment);

          this.setState({
            comment: '',
            video: yo
          });
        } else {
          console.log('Error');
        }
      })
  }

  render() {
    let thingy = 'http://138.68.12.208/uploads/' + this.state.video.vid;

    let finaldate = '';
    if (this.state.video.uploaded) {
      finaldate = this.state.video.uploaded;
      finaldate = finaldate.split('T')[0].split('-');
      finaldate = finaldate[1] + '/' + finaldate[2] + '/' + finaldate[0];
    }
    
    
    return (
      <div>
        <div className="top-bg">
          <Header />

          <div>
      

            <div id="recommended-video">
              <div id="vid" className="recommended-item">
                <center>
                  <video width="720" height="480"  src={thingy} controls id="noContextMenu">
                    Video is not supported in your browser.
                  </video>
                </center>
              </div>
              <div id="suggestions" className="recommended-item">
                {this.state.suggested ? 
                  this.state.suggested.map((item, i) => {
                    return (
                      
                      <Link key={i} style={{textDecoration: 'none', color: 'white'}} to={"/video/" + item._id}>
                        <div className="suggested-item">                          
                          <img src={"http://138.68.12.208/uploads/" + item.thumbnail} width="168" height="94" style={{gridRowStart: '1', gridRowEnd: '3'}} />                          
                          <p>{item.title}</p>                                                    
                          <p style={{color: 'gray'}}>By: <Creatorstuff id={item.creator} /> </p>                          
                        </div>
                      </Link>
                      
                    )
                  })
                 : null}
                
              </div>
              <div id="description" className="recommended-item">
                <h2>{this.state.video.title}</h2>
                <h3>Uploaded: {finaldate}</h3>
                <Link to={"/profile/" + this.state.creatordata.name} style={{textDecoration: 'none', color: 'inherit'}}><h3 className="profile-link">By: {this.state.creatordata.name}</h3></Link>
                {this.state.subscribed ? 'Subscribed' : <button onClick={this.subscribe} className="subscribe-btn">Subscribe</button>}
                <div>
                  <h3>{this.state.video.likes.length} 
                  {this.state.video.likes.length > 1 || this.state.video.likes.length === 0 ? ' Likes' : ' Like'}</h3> 
                  {this.state.liked 
                  ? 
                  <React.Fragment><p>Video Liked</p></React.Fragment> : 
                  <button onClick={this.like} className="subscribe-btn">Like</button>}
                </div>
                <h3>Description: {this.state.video.description}</h3>
                
              </div>

              <div id="comments" className="recommended-item">
                {this.state.video.comments ? 
                    this.state.video.comments.map((item, i) => {
                      let date3 = item.written;
                      date3 = date3.split('T')[0].split('-');
                      date3 = date3[1] + '/' + date3[2] + '/' + date3[0];
                      return (                                              
                        <div key={i} className="comment">                                                                                
                          <p><Link style={{textDecoration: 'none', color: 'gray'}} to={"/profile/" + item.username}><span style={{color: 'gray'}}>{item.username}</span></Link> | <span style={{color: 'gray'}}>{date3}</span></p>                                                    
                          <h4>{item.content}</h4>                          
                        </div>                                               
                      )
                    })
                  : null}
                
                <div className="comment" style={{textAlign: 'center'}}>
                  <textarea onChange={this.onComment} value={this.state.comment} name="" id="" cols="98" rows="10"></textarea><br />
                  <button onClick={this.submitComment} className="subscribe-btn">Comment</button>
                </div>
              </div>
                
            </div>
          </div>          

          <Footer />
        </div>
      </div>
    )
  }
}

export default Video
Video.contextType = MyContext



