import React, { Component } from 'react'

import Header from './Header.js'
import Footer from './Footer.js'

import { MyContext } from '../storage'

import './css/Register.css'
import { setInStorage } from '../utils/storage.js';
import { getSomeStuffs } from '../utils/somefuncs'

export class Register extends Component {
  constructor(props) {
    super(props);

    this.state = {
      register: this.props.register,
      name: '',
      email: '',
      password: ''
    };

    this.linkOnClick = this.linkOnClick.bind(this);

    this.onName = this.onName.bind(this);
    this.onEmail = this.onEmail.bind(this);
    this.onPassword = this.onPassword.bind(this);

    this.onRegister = this.onRegister.bind(this);
    this.onSignIn = this.onSignIn.bind(this);
  }

  onName(e) {
    this.setState({
      name: e.target.value
    });
  }

  onEmail(e) {
    this.setState({
      email: e.target.value
    });
  }

  onPassword(e) {
    this.setState({
      password: e.target.value
    });
  }

  onRegister() {
    const {
      name,
      email,
      password
    } = this.state;

    fetch('/api/users/register', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      },
      body: JSON.stringify({
        name: name,
        email: email,
        password: password
      })
    }).then(res => res.json())
      .then(json => {
        if (json.success) {
          this.setState({
            register: false,
            email: '',
            name: '',
            password: ''
          });
        } else {
          console.log('Error');
        }
      })
  }

  onSignIn() {
    const {
      email,
      password
    } = this.state;

    fetch('/api/users/signin', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      },
      body: JSON.stringify({
        email: email,
        password: password
      })
    }).then(res => res.json())
      .then(json => {
        
        if(json.success) {
          setInStorage('eee_err', {token: json.token});
          this.setState({
            email: '',
            password: ''
          });
          //set token in context with this.context
          this.context.changeToken(json.token);
          //set userdata in context
          getSomeStuffs(json.token, (data) => {
            if (data.message) {
              console.log('error')
            } else {
              this.context.setUserData(data);
              this.props.changeToken(json.token);
            }
          });

        } else {
          console.log(json.message);
        }

      })

  }

  linkOnClick() {

    this.setState({
      register: !this.state.register
    });
  }

  render() {
    const {
      name,
      email,
      password
    } = this.state;
    
    return (
      <div>
        <div className="top-bg">

          <Header />

          <div className="wrapper-register">

            <div id="recommended">
              {this.state.register ?
                <div className="recommended-item">
                  <center>
                    <input type="text" className="title-input" name="name" placeholder="Name" value={name} onChange={this.onName} />
                  </center>
                </div> : null}

              <div className="recommended-item">
                <center>
                  <input type="email" className="title-input" name="email" placeholder="Email" value={email} onChange={this.onEmail} />
                </center>
              </div>

              <div className="recommended-item">
                <center>
                  <input type="password" className="title-input" name="password" placeholder="Password" value={password} onChange={this.onPassword} />
                </center>
              </div>


              <div className="recommended-item">
                <center>
                  {this.state.register ?
                  <button onClick={this.onRegister}>Create Account</button> :
                  <button onClick={this.onSignIn}>Sign In</button>}
                </center>
              </div>

              <div className="recommended-item">
                <center>
                  <button onClick={this.linkOnClick} style={{ textDecoration: 'underline' }}>
                    {
                      this.state.register ? 'I already have an account'
                        : "I don't have an account"
                    }

                  </button>


                </center>
              </div>

            </div>


          </div>
          <Footer />
        </div>
      </div>
    )
  }
}

export default Register
Register.contextType = MyContext;
