import React, { Component } from 'react'

import Header from './Header.js'
import Footer from './Footer.js'

import './css/Upload.css'

import {MyContext} from '../storage'

export class Upload extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userdata: {},
      phase1: true,
      file1: '',
      filename1: 'Choose Video',
      file2: '',
      filename2: 'Choose Thumbnail',
      title: '',
      description: '',
      creator: '',
      vidid: '',
      uploading: false
    };

    this.onChange1 = this.onChange1.bind(this);
    this.onChange2 = this.onChange2.bind(this);
    this.onTitle = this.onTitle.bind(this);
    this.onDescription = this.onDescription.bind(this);
    this.onButton1 = this.onButton1.bind(this);
    this.onButton2 = this.onButton2.bind(this);
  }

  //Add Upload functionality with React
  componentDidMount() {
    this.setState({userdata: this.context.state.userData, creator: this.context.state.userData._id});

  }

  onChange1(e) {
    this.setState({file1: e.target.files[0], filename1: e.target.files[0].name});
  }

  onChange2(e) {
    this.setState({file2: e.target.files[0], filename2: e.target.files[0].name});
  }

  onTitle(e) {
    this.setState({
      title: e.target.value
    });
  }

  onDescription(e) {
    this.setState({
      description: e.target.value
    });
  }

  onButton1(e) {
    e.preventDefault();
    this.setState({
      phase1: false
    });
    

    
  } 

  async onButton2(e) {
    e.preventDefault();
    this.setState({uploading: true});
    const formData = new FormData();
    formData.append('file1', this.state.file1);
    formData.append('file2', this.state.file2);
    formData.append('title', this.state.title);
    formData.append('description', this.state.description);
    formData.append('creator', this.state.creator);
    
    try {

      const res = await fetch('/api/videos/upload', {
        method: 'POST',
        headers: {
          
          'Access-Control-Allow-Origin': '*'
        },
        body: formData
      })

      
      
      this.setState({  
        file1: '',
        filename1: 'Choose Video',
        file2: '',
        filename2: 'Choose Thumbnail',
        title: '',
        description: '',
        vidid: '',
        phase1: true,
        uploading: false
      });

    } catch (err) {
      console.log(err);
    }
  } 

  render() {
    return (
      <div>
        <div className="top-bg">
          <Header />

          <div>
            { this.state.phase1 ?
            <div id="recommended-upload">
              <div className="recommended-item">
                <center>
                <label htmlFor="vid-upload" className="choose-vid">
                  <i></i>
                  {this.state.filename1}
                </label>
                <input id="vid-upload" name="vid" type="file" onChange={this.onChange1}/>
                </center>
              </div>

              

              <div className="recommended-item">
                <center>
                  <input type="text" id="title-input" name="title" placeholder="Title" onChange={this.onTitle}/>
                </center>
              </div>

              <div className="recommended-item">
                <center>
                  <label htmlFor="description-input">Description</label>
                  <textarea onChange={this.onDescription} name="description" id="description-input" cols="170" rows="10" />
                </center>
              </div>

              <div className="recommended-item">
                <center>
                  <button onClick={this.onButton1} style={{width: '10%'}}> Next </button>
                </center>
              </div>

            </div> : 
            <div id="recommended-upload">
              <div className="recommended-item">
                <center>
                  <label htmlFor="thumbnail-upload" className="choose-pic">
                    <i></i>
                    {this.state.filename2}
                  </label>
                  <input onChange={this.onChange2} id="thumbnail-upload" name="pic" type="file" />
                </center>
              </div>

              <div className="recommended-item">
                <center>
                  <button onClick={this.onButton2} style={{width: '10%'}}>Upload Video</button>
                  {this.state.uploading ? <p>Uploading...</p> : null}
                </center>
              </div>
            </div>
            }
          </div>

          <Footer />
        </div>
      </div>
    )
  }
}

export default Upload
Upload.contextType = MyContext
