import React, { Component } from 'react'
import {Link} from 'react-router-dom'

import Header from './Header.js'
import Footer from './Footer.js'
import Creatorstuff from './Creatorstuff'

import topBg from './top-bg.jpg';

import './css/Home.css'


import {MyContext} from '../storage'

import {
  getFromStorage
} from '../utils/storage';

export class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
      error: "",
      token: '',
      suggested: []
    };

  }

  componentDidMount() {
    fetch('/api/videos/all')
      .then(res3 => res3.json())
      .then(json3 => {
        this.setState({suggested: json3.videos});
      })
  }

  render() {
    
    return (
      <div>
        <div className="top-bg">
          <Header />
          <button style={{opacity: '0%'}}></button>
          <div className="wrapper">
            <div style={{backgroundColor: 'black', marginTop: '10vh', borderTop: '1px white solid'}}>
              <h1 style={{padding: '10px'}}>Recommended</h1>
            </div>

            <div id="home-recommended">
              {this.state.suggested ? 
                this.state.suggested.slice(0, 8).map((item, i) => {
                  return (
                    <div className="recommended-item" key={i}>
                      <center>
                        <Link to={'/video/' + item._id}>
                          <img width="272px" height="153px" src={"http://138.68.12.208/uploads/"+ item.thumbnail} alt=""/>
                        </Link>
                        <div style={{display: 'grid', gridTemplateColumns: '50% 50%'}}>
                          <Link style={{textDecoration: 'none', color: 'white'}} to={"/video/" + item._id}><p>{item.title}</p></Link>
                          <Link style={{textDecoration: 'none', color: 'gray'}} to={"/video/" + item._id}><p><Creatorstuff id={item.creator} /></p></Link>
                        </div>
                      </center>
                    </div>
                  )
                })
              : null}

              
              
            </div>
          </div>
          
          <Footer />
        </div>
        
      </div>
    )
  }
}

export default Home
Home.contextType = MyContext
