import React, { Component, useState, useEffect } from 'react'
import {Link} from 'react-router-dom'

import Header from './Header.js'
import Footer from './Footer.js'
import Creatorstuff from './Creatorstuff'

import {MyContext} from '../storage'

import './css/Profile.css'

import {getFromStorage} from '../utils/storage'

import { getUserFromId } from '../utils/somefuncs'

import {Redirect} from 'react-router-dom'

export class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: this.props.match.params.username,
      userdata: {},
      profiledata: {
        _id: '',
        name: '',
        email: '',
        subscribers: [],
        videos: []
      },
      subscribed: false,
      redirect: false
    };

    this.logout = this.logout.bind(this);
    this.subscribe = this.subscribe.bind(this);
  }

  

  componentWillReceiveProps(nextProps) {
    this.setState({username: nextProps.match.params.username});
    fetch('/api/users/userdatafromname?name=' + decodeURI(nextProps.match.params.username))
    .then(res => res.json())
    .then(json => {
      if (json.success) {
        console.log(json.user);
        this.setState({profiledata: json.user, userdata: this.context.state.userData});
        this.state.profiledata.subscribers.forEach(elem => {
          console.log(elem, this.state.userdata._id);
          if (elem === this.state.userdata._id) {
            this.setState({subscribed: true});
          }
            
        });
      } else {
        console.log(json.message);
      }
    })
  
  }

 

  componentDidMount() {

    console.log(this.context.state.userData);

    fetch('/api/users/userdatafromname?name=' + decodeURI(this.state.username))
      .then(res => res.json())
      .then(json => {
        if (json.success) {
          console.log(json.user);
          this.setState({profiledata: json.user, userdata: this.context.state.userData});
          this.state.profiledata.subscribers.forEach(elem => {
            console.log(elem, this.state.userdata._id);
            if (elem === this.state.userdata._id) {
              this.setState({subscribed: true});
            }
              
          });
        } else {
          console.log(json.message);
        }
      })
    
  }

  subscribe() {
    const userid1 = this.state.userdata._id;
    const name = this.state.profiledata.name;

    fetch('/api/users/subscribe', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      },
      body: JSON.stringify({
        name: name,
        userid1: userid1
      })
    }).then(res => res.json())
      .then(json => {
        if (json.success) {
          let hi = this.state.profiledata
          let thing = hi.subscribers.push(userid1);

          this.setState({
            subscribed: true,
            profiledata: hi
          });
        } else {
          console.log('Error');
        }
      })
  }

  logout() {
    const obj = getFromStorage('eee_err');

    if (obj && obj.token) {
      fetch('/api/users/logout?token=' + obj.token)
        .then(res => res.json())
        .then(json => {
          if(json.success) {
            this.props.setParentToken('');
            this.context.changeToken('');
            this.context.setUserData({});
            

          } else {
            console.log(json.message);
          }
        })
    } else {
      console.log('error');
    }
  }

  render() {
    const {
      username
    } = this.state;
    console.log(this.state);

    if (this.state.redirect) {
      return (
        <Redirect to="/auth" />
      )
    }

    return (

      <div>
        <div className="top-bg">
          <Header />
          <div id="profile" >
            <div className="profile-section section0">
              <hr width="1" size="1%" style={{opacity: '100%'}} />
              <h1>{this.state.profiledata.name}</h1>
              <hr width="1" size="1%" />
              <p>Videos: {this.state.profiledata.videos.length}</p>
              <hr width="1" size="1%" />
              <p>Subscribers: {this.state.profiledata.subscribers.length}</p>
              <hr width="1" size="1%" />
              {
                this.state.subscribed ? <p>Subscribed</p>
                : <button onClick={this.subscribe} className="subscribe-btn">Subscribe</button>
              }
              
              <hr width="1" size="1%" style={{opacity: '100%'}} />
            </div>
            {
              this.state.userdata.name === username ? <button style={{margin: '5px'}} onClick={this.logout}>Logout</button>
              : null
            }
            <div className="profile-section section2" style={{minHeight: '55vh'}}>
              {this.state.profiledata.videos.length < 4 ? <h1>This account has less than 4 videos.</h1>
              : <div id={"recommended-profile"}>
                {this.state.profiledata.videos ? 
                  this.state.profiledata.videos.slice(0, 6).map((item, i) => {
                    return <Videothing key={i} id={item}/>
                  })  
                : null}
                
              </div>}

            </div>
          </div>
          <Footer />
        </div>
      </div>
    )
  }
}

const Videothing = (props) => {
  const [id, setId] = useState(props.id);
  const [videostuff, setVideostuff] = useState({});
  const [holy, setHoly] = useState(<React.Fragment></React.Fragment>);
  useEffect(() => {
    console.log('hi');
    fetch('/api/videos/one?id=' + id)
      .then(res => res.json())
      .then(json => {
        if (json.success) {
          console.log(json)
          setVideostuff(json.video);
          console.log(videostuff);
          setHoly(
            <div className="recommended-item" key={props.key}>
              <center>
                <Link to={'/video/' + json.video._id}>
                  <img width="272px" height="153px" src={"http://138.68.12.208/uploads/" + json.video.thumbnail} alt=""/>
                </Link>
                <div style={{display: 'grid', gridTemplateColumns: '50% 50%'}}>
                  <Link style={{textDecoration: 'none', color: 'white'}} to={'/video/' + json.video._id}><p>{json.video.title}</p></Link>
                  <Link style={{textDecoration: 'none', color: 'gray'}} to={'/video/' + json.video._id}><p><Creatorstuff id={json.video.creator} /></p></Link>
                </div>
              </center>
            </div>
          );
          console.log(holy);
          
        } else {
          setHoly(<React.Fragment>Error</React.Fragment>);
        }
      })
  }, [id]);
  return holy;
};

export default Profile
Profile.contextType = MyContext
